public sendFormData(formData) {
    return this.http.post<any>(this.serverUrl, formData, {
      reportProgress: true,
      observe: 'events'
    });
}
sendFile(file) {
  const formData = new FormData();
  formData.append('file', file.data);
  file.inProgress = true;
  this.sendFormData(formData).subscribe((event: any) => {
      if (typeof (event) === 'object') {
        // console.log(event.body);
      }
    });
}
private sendFiles() {
  this.fileUpload.nativeElement.value = '';
  this.files.forEach(file => {
    this.sendFile(file);
  });
}
onClick() {
  const fileUpload = this.fileUpload.nativeElement;fileUpload.onchange = () => {
  for (let index = 0; index < fileUpload.files.length; index++)
  {
   const file = fileUpload.files[index];
   this.files.push({ data: file, inProgress: false, progress: 0});
  }
    this.sendFiles();
  };
  fileUpload.click();
}


  deleteRow(row) {

    Swal.fire({
      title: 'Are you sure?',
      text: 'Please check the dependency before deleted',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes, delete it!',
      cancelButtonText: 'No, keep it'
    }).then((result) => {
      if (result.value) {
        this.data = this.arrayRemove(this.data, row.id);
        this.http.delete(this.mainurl + 'emp/'+row.id).subscribe();
        this.showNotification(
          'bg-red',
          'Delete Record Successfully',
          'bottom',
          'right'
        );
        Swal.fire(
          'Deleted!',
          'Seleted data is  been deleted..',
          'success'
        )
      // For more information about handling dismissals please visit
      // https://sweetalert2.github.io/#handling-dismissals
      } else if (result.dismiss === Swal.DismissReason.cancel) {
        Swal.fire(
          'Cancelled',
          'Seleted data is not deleted:)',
          'error'
        )
      }
    })
    // this.data = this.arrayRemove(this.data, row.id);
    // this.showNotification(
    //   'bg-red',
    //   'Delete Record Successfully',
    //   'bottom',
    //   'right'
    // );

  }

  arrayRemove(array, id) {
    return array.filter(function(element) {
      return element.id != id;
    });
  }
